@import url('https://webfontworld.github.io/Dokdo/Dokdo.css');
@import url('https://fonts.googleapis.com/css2?family=Gaegu:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all 0.2s;

  ul,
  ol,
  li {
    list-style: none;
  }

  a {
    text-decoration: none;
    color: #000;
  }
}

.App {
  text-align: center;
  font-family: 'Gaegu', cursive;
  background-color: #FFD9ED;
  min-height: 100vh;

  .app_wrap {
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }

  .title {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;

    h1 {
      font-family: 'Dokdo';
      font-size: calc(70px + 2vmin);
      line-height: 70px;
      word-wrap: break-word;
      word-break: keep-all;
      white-space: pre-line;
      color: #307755;

      position: relative;
      display: inline-flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    h1::before {
      content: '';
      display: block;
      width: calc(60px + 2vmin);
      height: calc(30px + 2vmin);
      background: url(../public/img/sprout.svg) center no-repeat;
      background-size: contain;
    }
  }

  .profile {
    min-height: 100vh;
    position: relative;
    font-size: calc(10px + 2vmin);

    .edu {
      display: inline-block;
      padding: 20px;
      background-color: #fff;
      border: 3px dashed #47399C;
      word-wrap: break-word;
      word-break: keep-all;
      white-space: pre-line;

      position: absolute;
      top: calc(50% - 280px);
      left: calc(50% - 530px);
    }

    .skill {
      position: absolute;
      top: calc(50% - 350px);
      left: calc(50% - 70px);

      .skill_contents {
        display: flex;
        justify-content: space-around;
        background-color: #fff;
        width: 400px;
        height: 250px;
        padding: 30px 10px;
        position: absolute;
        z-index: 3;
      }
    }

    .skill::after {
      content: '';
      display: block;
      border: 1px solid #F45564;
      width: 400px;
      height: 250px;
      position: absolute;
      top: 20px;
      left: 20px;
      z-index: 2;
    }

    .photo {
      position: absolute;
      top: calc(50% - 0px);
      left: calc(50% - 50px);

      .photo_contents {
        border: 3px solid #fff;
        width: 180px;
        height: 238px;
        box-shadow: 20px 20px 0 #307755;
        background: url(../public/img/photo.jpg) center no-repeat;
        background-size: contain;
      }

      .photo_contents::after {
        content: '';
        display: block;
        border: 1px solid #FF008A;
        width: 180px;
        height: 238px;
        position: absolute;
        top: -20px;
        left: -20px;
      }

      .download {
        position: absolute;
        top: -20px;
        left: 190px;
        width: 200px;
        height: 150px;
        background: url(../public/img/download.svg) center no-repeat;
        background-size: contain;
        display: flex;
        align-items: center;
        justify-content: center;

        p {
          
          a {
            color: #fff;
            word-wrap: break-word;
            word-break: keep-all;
            white-space: pre-line;
            display: block;
          }

          a:hover {
            color: #F1FF99;
            text-shadow: 0 0 5px #F1FF99;
            transform: translate(5px,5px);
          }
        }
      }
    }

    .info {
      position: absolute;
      top: calc(50% - 120px);
      left: calc(50% - 480px);

      .info_contents {
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-color: #fff;
        width: 300px;
        height: 220px;
        padding: 20px 10px;
        word-wrap: break-word;
        word-break: keep-all;
        white-space: pre-line;
        box-shadow: 20px 20px 0 #FF008A;
      }
    }

    .flower {
      &1 {
        width: 100px;
        height: 100px;
        background: url(../public/img/flower_pink.svg) center no-repeat;
        background-size: contain;

        position: absolute;
        top: calc(50% - 100px);
        right: calc(20% + 30px);
      }

      &2 {
        width: 100px;
        height: 100px;
        background: url(../public/img/flower_green.svg) center no-repeat;
        background-size: contain;

        position: absolute;
        top: calc(50% - 10px);
        right: calc(20% + 100px);
      }

      &3 {
        width: 100px;
        height: 100px;
        background: url(../public/img/flower_navy.svg) center no-repeat;
        background-size: contain;

        position: absolute;
        top: calc(50% + 150px);
        right: calc(50% - 300px);
      }
    }
  }

  .work {
    height: 100vh;

    .swiper {
      width: 100%;
      height: 100%;

      .work_avatar {
        .desktop {
          .click {
            img {
              height: 100%;
            }
          }
        }
      }

      .swiper-slide {
        text-align: center;
        font-size: calc(10px + 2vmin);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        h1 {
          color: #FF008A;

          span {
            display: block;
            font-size: calc(5px + 2vmin);
            margin-top: -15px;
            margin-bottom: 15px;
          }
        }

        .mockup {
          position: relative;
          display: inline-flex;
          align-items: flex-end;
          justify-content: center;

          .desktop {
            width: 700px;
            height: 500px;
            background: url(../public/img/desktop.png) center no-repeat;
            background-size: contain;
            position: relative;

            .click {
              width: 483px;
              height: 305px;
              overflow: hidden;
              position: relative;
              top: 65px;
              left: 109px;
              cursor: pointer;

              a {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(#307755, 0.5) ;
                color: #fff;
                display: inline-flex;
                justify-content: center;
                align-items: center;

                opacity: 0;
              }

              a:hover {
                opacity: 1;
              }

              img {
                width: 100%;
              }
            }
          }

          .smartphone {
            width: 100px;
            height: 200px;
            background: url(../public/img/smartphone.png) center no-repeat;
            background-size: contain;

            .click {
              width: 88px;
              height: 185px;
              border-radius: 10%;
              overflow: hidden;
              position: relative;
              top: 7px;
              left: 6px;
              cursor: pointer;

              img {
                width: 100%;
              }
            }
          }
        }
      }

      .swiper-button-prev,
      .swiper-button-next {
        width: 100px;
        height: 100px;
      }

      .swiper-button-prev::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background: url(../public/img/flower_green.svg) center no-repeat;
        background-size: contain;
      }

      .swiper-button-next::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background: url(../public/img/flower_green.svg) center no-repeat;
        background-size: contain;
      }
    }
  }

  .pagenation {
    position: fixed;
    bottom: 50px;
    font-size: calc(14px + 2vmin);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 999;

    ul {
      display: flex;
      align-items: center;
      justify-content: space-around;

      li {
        position: relative;

        a {
          padding: 10px 20px;
        }

        a:hover,
        a.active {
          color: #307755;
          font-weight: bold;
        }

        a.active::after {
          content: '';
          display: block;
          position: absolute;
          top: calc(-10px + -2vmin);
          left: calc(14px + 2vmin);
          width: calc(20px + 2vmin);
          height: calc(18px + 2vmin);
          background: url(../public/img/sprout.svg) center no-repeat;
          background-size: contain;
        }
      }
    }
  }
}

@media (prefers-reduced-motion: no-preference) {
  .flower1,
  .flower2,
  .flower3 {
    animation: flower-spin infinite 20s linear;
  }

  .work_spao,
  .work_keroppi {
    .smartphone:hover {
      img {
        animation: scroll infinite 10s ease-in-out;
      }
    }
  }
}

@keyframes flower-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes scroll {
  from {
    transform: translateY(0%);
  }

  to {
    transform: translateY(-50%);
  }
}

